html {
    /* this makes sure the padding and the border is included in the box sizing */
    box-sizing: border-box;
    overflow-y: hidden;
    overflow-x: none;
    width: 100vw;
}

html body {
    padding: 0;
    margin: 0;
    overflow-y: inherit;
    overflow-x: none;
    width: 100vw;
}